<template>
  <div class="about" v-loading="loading2"
    element-loading-text="拼命提交中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)">
    <div class="head">
      <p>
        <i class="fa fa-superpowers" aria-hidden="true"></i> DappShow</p>
    </div>
    <div class="info">
      <hr>
      <el-row>
        <div id="submit-form">
          <div>
            <h1>Submit a DApp</h1>
            <span>填写您的Dapp信息,同时提供一些NAS奖励(不少于0.01NAS)作为宣传使用,玩家在游戏过程中提交分数之后,会弹出您的DAPP信息,玩家可以选择获得您提供的奖励,同时也提供了跳转到您的DAPP的入口.如果您重复提交相同一个DAPP,会累积奖金,但是不会作为两个DAPP宣传</span>
          </div>
        </div>
        <el-form label-position="top" ref="appForm" :rules="rules" label-width="80px" :model="appForm" style="text-align: left;margin:40px 0;">
          <el-form-item label="DAPP名称：" prop="name">
            <el-input v-model="appForm.name" placeholder="请输入您的DAPP名称"></el-input>
          </el-form-item>
          <el-form-item label="开发者：" prop="dev">
            <el-input v-model="appForm.dev" placeholder="请输入作者的钱包地址"></el-input>
          </el-form-item>
          <el-form-item label="APP类型：" prop="type">
            <el-select style="width: 100%" v-model="appForm.type">
              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="链接：" prop="link">
            <el-input v-model="appForm.link" placeholder="请输入应用链接"></el-input>
          </el-form-item>
          <el-form-item label="总奖励：" prop="reward">
            <el-input v-model.number="appForm.reward" placeholder="请输入总奖励额度"></el-input>
          </el-form-item>
          <el-form-item label="介绍：" prop="intro">
            <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 6 }" v-model="appForm.intro" placeholder="请输入介绍"></el-input>
          </el-form-item>
          <el-form-item label="应用图标：">
            <el-button class="avatar-uploader">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-button>
          </el-form-item>
          <el-form-item>
            <input type="file" @change="upimg" accept="image/*" />
          </el-form-item>
          <el-form-item>
            <el-button type="success" style="width:100%" @click="submitForm">提交</el-button>
          </el-form-item>
        </el-form>
      </el-row>
    </div>
  </div>
</template>

<script>
import NebPay from "nebpay";
const nebPay = new NebPay();
var dappAddress = "n1gsLRXcyUKmrD9unVfgB4QyEMNoTJ4ziSJ";
var netbase = "https://mainnet.nebulas.io";
const api = function(callFunction, param = [], value, needPay = false) {
  return new Promise((resolve, reject) => {
    const to = dappAddress;
    const callArgs = JSON.stringify(param);
    const way = needPay ? "call" : "simulateCall";
    nebPay[way](to, value, callFunction, callArgs, {
      listener: res => {
        console.log("listener", res);
        try {
          resolve(res);
        } catch (e) {
          reject(res);
        }
      }
    });
  });
};

export default {
  mounted() {
    this.appForm.dev = this.getCookie("myaddress")
    this.loading2 = false
  },
  data() {
    return {
      imageUrl: "",
      loading2: false,
      options: [
        {
          label: "Application(应用)",
          value: "Application"
        },
        {
          label: "Game(游戏)",
          value: "Game"
        },
        {
          label: "Other(其它)",
          value: "Other"
        }
      ],
      appForm: {
        name: "",
        type: "",
        dev: "",
        link: "",
        reward: undefined,
        intro: ""
      },
      rules: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        dev: [
          { required: true, message: "获取开发者名称发生错误", trigger: "blur" }
        ],
        type: [
          { required: true, message: "请选择应用类型", trigger: "change" }
        ],
        link: [{ required: true, message: "请输入链接", trigger: "blur" }],
        reward: [
          {
            required: true,
            type: "number",
            message: "请输入正确数字",
            trigger: "blur"
          },
          {
            validator: (rule, value, callback) => {
              if(value < 0.01) {
                callback(new Error("奖励金需要大于等于0.01"));
              } else {
                callback();
              }
            }, trigger: 'blur'
          }
        ],
        intro: [{ required: true, message: "请输入介绍", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs["appForm"].validate(valid => {
        if (valid && this.imageUrl !== "") {
          var callArgs = [];
          var dappName = this.appForm.name;
          var dappDev = this.appForm.dev;
          var dappType = this.appForm.type;
          var dappWebUrl = this.appForm.link;
          var dappPic = this.imageUrl;
          var dappDescription = this.appForm.intro;
          var amount = this.appForm.reward;
          callArgs.push(dappName);
          callArgs.push(dappDev);
          callArgs.push(dappType);
          callArgs.push(dappWebUrl);
          callArgs.push(dappPic);
          callArgs.push(dappDescription);
          this.loading2 = true

          api("upDapp", callArgs, amount, true)
            .then(resp => {
              console.log('updapp-hash', resp.txhash)
              if(typeof resp == 'string') {
                if (resp.indexOf('Error') != -1) {
                  this.$message.error(resp)
                  this.loading2 = false
                }
              } else {
                if (resp.txhash !== "") {
                  console.log("success-upDapp", resp);
                  this.cbPush(resp);
                } else {
                  console.log("error-upDapp", resp);
                  this.$message.error(resp);
                }
              }
            })
            .catch(resp => {
              console.log("listener-catch-error", resp);
            });
        } else {
          console.log("error submit!!");
          if (this.imageUrl === "") {
            this.$message.error("请务必选择DAPP的图标o(╯□╰)o");
          }
          return false;
        }
      });
    },
    cbPush(resp) {
      if (resp) {
        if (resp.txhash) {
          var txhash = resp.txhash;
          let trigger = setInterval(() => {
            var nebulas = require("nebulas"),
              Account = nebulas.Account,
              neb = new nebulas.Neb();
            neb.setRequest(new nebulas.HttpRequest(netbase));

            neb.api.getTransactionReceipt({ hash: txhash }).then(receipt => {
              console.log("status", receipt.status);

              if (receipt.status != 2) {
                //not in pending
                console.log(JSON.stringify(receipt));
                if (receipt.status == 1) {
                  clearInterval(trigger);
                  this.$message.success("提交成功");
                  this.resetForm();
                } else {
                  clearInterval(trigger);
                  this.$message.info(receipt.execute_error);
                  console.log('status-0', receipt)
                }
                this.loading2 = false
              } else {
                console.log("wisteria log:" + JSON.stringify(receipt));
              }
            });
          }, 4000);
        } else {
          this.$message.info(JSON.parse(resp));
        }
      }
    },
    resetForm() {
      this.$refs["appForm"].resetFields();
    },
    getCookie(cname) {
      var name = cname + "=";
      var ca = document.cookie.split(";");
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == " ") c = c.substring(1);
        if (c.indexOf(name) != -1) return c.substring(name.length, c.length);
      }
      return "";
    },
    dataURLtoBlob(dataurl) {
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    scaleimg(img) {
      let height = img.currentTarget.height;
      let width = img.currentTarget.width;
      let base64data = "";
      let can = document.createElement("canvas");
      can.height = height;
      can.width = width;
      let casimg = can.getContext("2d");
      casimg.clearRect(0, 0, width, height);
      casimg.drawImage(img.currentTarget, 0, 0, width, height);
      base64data = can.toDataURL("image/jpeg"); //获取在canvas元素中的图片截图base64编码
      let size = Math.round(this.dataURLtoBlob(base64data).size / 1024); //获取压缩前的图片大小
      let maxsize = 10; //设置压缩后的最大值
      if (size > maxsize) {
        if (size < 1300) {
          img.currentTarget.height = Math.round(height * (3 / 4));
          img.currentTarget.width = Math.round(width * (3 / 4));
          return this.scaleimg(img);
        } else if (size < 1800) {
          img.currentTarget.height = Math.round(height * (2 / 3));
          img.currentTarget.width = Math.round(width * (2 / 3));
          return this.scaleimg(img);
        } else {
          img.currentTarget.height = Math.round(height / 2);
          img.currentTarget.width = Math.round(width / 2);
          return this.scaleimg(img);
        }
      } else {
        console.log("压缩后大小" + size);
        return base64data;
      }
    },
    upimg(e) {
      let name = e.target.name;
      let file = e.target.files[0];
      if (!file) {
        return;
      }
      let filesize = file.size / 1024;
      let data = new FileReader();
      let img = new Image();
      let formatdata = "";
      data.onload = e1 => {
        img.onload = e => {
          console.log(
            "压缩前大小" + this.dataURLtoBlob(e1.target.result).size / 1024
          ); //压缩前大小
          if (/^image\/([a-zA-Z]|\*)+$/.test(file.type)) {
            formatdata = this.scaleimg(e);
            this.imageUrl = formatdata;
          }
        };
        img.src = e1.target.result; //触发加载img load事件
      };
      data.readAsDataURL(file); //触发加载filereader load事件
    }
  }
};
</script>

<style lang="scss" scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.info {
  max-width: 1200px;
  margin: 60px auto 0;
  text-align: start;
  .title {
    font-size: 20px;
    padding-left: 20px;
  }
  .row {
    i {
      font-size: 24px;
      padding: 0 20px;
    }
    height: 60px;
    display: flex;
    align-items: center;
    padding: 10px 30px;
    .name {
      font-size: 14px;
      color: #72635d;
    }
  }
  .pic {
    height: 320px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 50%;
    }
  }
  .code {
    display: flex;
    padding: 20px;
    justify-content: space-around;
    text-align: center;
    img {
      width: 220px;
    }
  }
}
</style>



